import './css/styles.css';
import { Application, settings } from 'pixi.js';
import { loadFonts } from './styles';
import { isState, StateMachine } from './state-machine';
import navLinks from './nav-links.json';
import TWEEN from '@tweenjs/tween.js';

settings.FAIL_IF_MAJOR_PERFORMANCE_CAVEAT = false;

window.onload = async () => {

  // await Promise.all([
  //   loadFonts(),
  // ]);

  try {
    const app = new Application({
      antialias: true,
      transparent: false,
      autoDensity: true,
      resolution: window.devicePixelRatio || 1,
      resizeTo: window,
      backgroundColor: 0x000000,
    });

    const resizeCanvas = () => {
      app.renderer.resolution = window.devicePixelRatio;
      app.resize();
    }
    // Change resolution if changes
    window.matchMedia(`(resolution: ${window.devicePixelRatio}dppx)`)
      .addEventListener("change", resizeCanvas);
    window.addEventListener("orientationchange", resizeCanvas);

    document.body.appendChild(app.view);

    const stateManager = new StateMachine(app);

    const stateHash = window.location.hash.replace(/^#/,'');

    if(stateHash && isState(stateHash)) {
      stateManager.setState(stateHash);
    } else {
      stateManager.setState("intro");
    }

    window.addEventListener('hashchange',function () {
      const stateHash = window.location.hash.replace(/^#/,'');

      if(isState(stateHash)) {
        stateManager.setState(stateHash);
      } else if(!stateHash) {
        stateManager.setState("intro");
      }
    });
  } catch (e) {
    const errorDiv = document.createElement('div');
    const errorDivMessage = document.createElement('pre');
    document.body.classList.add('load-error');
    errorDiv.classList.add('error-message');
    errorDivMessage.innerText = `Uh oh! Your browser had trouble setting things up. Your browser might not be supported.\n\nerror message:\n${e.message}`;
    errorDiv.appendChild(errorDivMessage);
    document.body.appendChild(errorDiv);
  }

  const footer = document.createElement('nav');
  const links = Object.entries(navLinks).map(([label, url]) => {
    const a = document.createElement('a');
    a.setAttribute('href', url);
    a.setAttribute('title', url.replace(/^.*:\/\//,'').replace(/^mailto:/,''));
    a.innerText = label;
    return a;
  });
  links.forEach((link) => footer.appendChild(link));
  document.body.appendChild(footer);

  const updateLink = () => {
    if(window.location.hash === "#musicians") {
      const link = links.find((e) => e.getAttribute('href') === "#musicians");
      if(link) {
        link.setAttribute("href", "#");
        link.innerText = "interactive";
        link.setAttribute('title', '/');
      }
    } else {
      const link = links.find((e) => e.getAttribute('href') === "#");
      if(link) {
        link.setAttribute("href", "#musicians");
        link.innerText = "musicians";
        link.setAttribute('title', '#musicians');
      }
    }
  }

  updateLink();
  // Toggle the muscians nav link
  window.addEventListener('hashchange', updateLink);

  // @TODO - replace this with app.ticker.add?
  function animate(time: number) {
    requestAnimationFrame(animate);
    TWEEN.update(time);
  }
  requestAnimationFrame(animate);
};