import './css/styles.css';
import { Application, Container, filters } from 'pixi.js';
import { BioElement } from './bio-element';
import { FancyViewport } from './fancy-viewport';
import { BIO_DATA } from './bio-data';
import { ClickEventData } from 'pixi-viewport';
import State from "./state";

export default class BiographiesState extends State {
  private viewport: FancyViewport;

  private bioElements: BioElement[];
  private bioContainer: Container;
  private initialWidth: number;
  private initialHeight: number;

  async createContainer(app: Application): Promise<Container> {
    this.viewport = new FancyViewport({
      screenWidth: window.innerWidth,
      screenHeight: window.innerHeight,
      interaction: app.renderer.plugins.interaction,
    });

    app.renderer.backgroundColor = 0x000000;

    this.viewport.viewPort.on("clicked", (d: ClickEventData) => {
      const {x, y} = d.screen;
      for (let i = 0; i < this.bioElements.length; i++) {
        const b = this.bioElements[i].getBounds(true);
        // Basic rectable*point collision
        if(x < b.x) continue;
        if(x > b.x + b.width) continue;
        if(y < b.y) continue;
        if(y > b.y + b.height) continue;
        return; // Cannot unfocus all, we've collided with a bioelement
      }
      unFocusAll(this.bioElements);
    });

    this.bioContainer = new Container();
    this.bioContainer.sortableChildren = true;
    this.viewport.addChild(this.bioContainer);

    this.bioElements = BIO_DATA.map((m) => new BioElement(m));

    this.bioElements.forEach((b) => {
      b.on("focused", () => {
        unFocusAll(this.bioElements, b);
      });
    });

    for (let i = 0; i < this.bioElements.length; i++) {
      const e = this.bioElements[i];
      this.bioContainer.addChild(e);
      const x = (i % 10) * 80;
      const y = Math.floor(i / 10) * 80;
      e.position.set(x, y);
    }

    const alphaFilter = new filters.AlphaFilter();
    const blurFilter = new filters.BlurFilter();
    blurFilter.blur = 1.7;
    alphaFilter.alpha = 0.5;

    function unFocusAll(allBios: Array<BioElement>, except?: BioElement) {
      if(except) {
        except.filters = [];
      }
      for (let i = 0; i < allBios.length; i++) {
        if(except === undefined || allBios[i] !== except) {
          allBios[i].unfocus();
          if(except) {
            allBios[i].filters = [blurFilter, alphaFilter];
          } else {
            allBios[i].filters = [];
          }
        }
      }
    }

    this.initialWidth = this.bioContainer.width;
    this.initialHeight = this.bioContainer.height;

    this.onResize();

    return this.viewport;
  }

  async cleanUp() {
    this.viewport.destroy({children: true});
    delete this.bioElements;
  }

  onTick(deltams) {
    if(this.bioElements) {
      this.bioElements.forEach((b) => {
        b.onTick(deltams);
      });
    }
  }

  onResize() {

    let paddTop = 0;
    if(!document.fullscreenElement) {
      const nav = document.getElementsByTagName("nav");
      if(nav && nav[0]) {
        const {y,height} = nav[0].getBoundingClientRect();
        paddTop = y + height;
      }
    }

    this.viewport.viewPort.screenWidth = window.innerWidth;
    this.viewport.viewPort.screenHeight = window.innerHeight - paddTop;
    this.viewport.position.y = paddTop;
    this.viewport.viewPort.moveCenter((this.initialWidth-64)/2, (this.initialHeight-64)/2)
    this.viewport.viewPort.fit(true, this.initialWidth+64, this.initialHeight+64);

  }

}