import { Container, Graphics, Sprite } from "pixi.js";
import Sound from 'pixi-sound';
import TWEEN from "@tweenjs/tween.js";

const PLAYER_SCRUB_SIZE = 4;

export class RoundSoundPlayer extends Container {
  protected maskGraphic: Graphics;
  public isLoaded: boolean = false;
  protected displaySprite: Sprite;
  public canInteract = true;
  protected playButtonSizeRatio = 0.1;
  statusText: string = "Unloaded";
  public autoplay: boolean = false;
  protected overlayGraphics: Graphics;
  protected sound: Sound.Sound;
  protected soundInstance: Sound.IMediaInstance;
  protected progress: number = 0;
  private animationFrameId: number;

  constructor(public soundUrl: string, public image?: Sprite, public playerRadius: number = 64, public accentColor: number = 0xffffff) {
    super();

    const background = new Graphics();
    background.beginFill(0x000000, 0.75);
    background.drawRect(0,0,this.playerRadius*2, this.playerRadius*2);
    background.endFill();
    this.addChild(background);
    this.maskGraphic = new Graphics();
    this.addChild(this.maskGraphic);
    this.mask = this.maskGraphic;
    this.maskGraphic.position.set(0,0);
    this.maskGraphic.beginFill(0xffffff, 1);
    this.maskGraphic.drawCircle(this.playerRadius,this.playerRadius,this.playerRadius);
    this.maskGraphic.endFill();

    if(this.image) {
      this.addChild(this.image);
      this.image.anchor.set(0.5,0.5);
      this.image.scale.set(0);
      this.image.alpha = 0;
      this.image.position.set(this.playerRadius, this.playerRadius);

      new TWEEN.Tween({alpha: 0, scale: 0.5*1.2*this.playerRadius/this.image.texture.width})
      .to({alpha: 1, scale: 1.2*this.playerRadius/this.image.texture.width}, 400)
      .easing( TWEEN.Easing.Elastic.Out )
      .onUpdate(({alpha, scale}) => {
        if(this.image) {
          this.image.alpha = alpha;
          this.image.scale.set(scale);
        }
      })
      .start();
    }

    this.interactive = true;
    this.cursor = "pointer";
    this.on('pointertap', () => {
      if(this.canInteract) {
        this.interact();
      }
    });

    this.overlayGraphics = new Graphics();
    this.addChild(this.overlayGraphics);
    this.overlayGraphics.position.set(this.playerRadius, this.playerRadius);
    this.updateGraphics();
  }

  interact() {
    this.playpause();
  }

  updateGraphics(loop: boolean = true) {
    if(loop) {
      this.animationFrameId = window.requestAnimationFrame(this.updateGraphics.bind(this));
    }
    this.overlayGraphics.clear();

    this.overlayGraphics.lineStyle(PLAYER_SCRUB_SIZE, 0x000000, 0.6, 0);
    this.overlayGraphics.drawCircle(0, 0, this.playerRadius);

    if(this.progress !== NaN && this.progress > 0) {
      this.overlayGraphics.lineStyle(PLAYER_SCRUB_SIZE + 5, this.accentColor, 1, 0);
      if(this.progress >= 1) {
        this.overlayGraphics.drawCircle(0, 0, this.playerRadius + 5);
      } else {
        const arc =  Math.PI * 2 * this.progress;
        this.overlayGraphics.arc(0, 0, this.playerRadius + 5, -Math.PI / 2, -Math.PI / 2 + arc);
      }
    }
  }

  playerEnded() {
    if(this.animationFrameId !== -1) {
      window.cancelAnimationFrame(this.animationFrameId);
      this.animationFrameId = -1;
    }
    this.emit("ended");
  }

  playerProgress(progress) {
    this.progress = progress;
  }

  playpause() {
    if(this.sound) {
      if(this.sound.paused) {
        this.sound.resume();
        this.emit("play");
        this.statusText = "Playing";
        if(this.animationFrameId === -1) {
          this.animationFrameId = window.requestAnimationFrame(this.updateGraphics.bind(this));
        }
      } else {
        this.sound.pause();
        this.emit("pause");
        this.statusText = "Paused";
        this.updateGraphics(false);
        if(this.animationFrameId !== -1) {
          window.cancelAnimationFrame(this.animationFrameId);
          this.animationFrameId = -1;
        }
      }
    }
  }

  destroy() {
    super.destroy({children: true});

    if(this.soundInstance) {
      this.soundInstance.off('progress', this.playerProgress.bind(this));
      this.soundInstance.off('end', this.playerEnded.bind(this));
      this.soundInstance.stop();
      this.soundInstance.destroy();
    }
    if(this.sound) {
      this.sound.destroy();
    }
  }

  async preload() {
    await new Promise((resolve, reject) => {
      Sound.Sound.from({
        url: this.soundUrl,
        preload: true,
        autoPlay: this.autoplay,
        loaded: async (err, sound) => {
          if(sound) {
            this.sound = sound;
            const instance = await sound.play();
            instance.on('progress', this.playerProgress.bind(this));
            instance.on('end', this.playerEnded.bind(this));
            return resolve();
          }

          return reject();
          }
      });
    });
  }
}