import { Instrument } from "./instruments";

type DateRange = {
  from: Date;
  to?: Date;
}

export type BasicPerson = {
  name: string;
}

export type Biography = BasicPerson & {
  donorLevel?: "donor";
  bioText?: string;
  title?: string;
  videos?: {
    [key: string]: string
  };
  photos?: string[];
}

export type Musician = Biography & {
  instrument: Instrument;
  yearStarted?: number;
  //whichSymphony: string;
}

export function isMusician(e: any): e is Musician {
  return !!e.instrument;
}