import { Container, Graphics, Text, Point, Sprite, AnimatedSprite, BLEND_MODES, DisplayObject } from "pixi.js";
import { Biography, isMusician, Musician } from "./types/musician";
import TWEEN from "@tweenjs/tween.js";
import { INSTRUMENT_COLORS, DEFAULT_INSTRUMENT_COLOR, SOME_ACCENT_COLORS, videoColor } from "./colors";
import { TEXT_STYLE_H2, TEXT_STYLE_BIO_P, TET_STYLE_BIO_SUBTITLE, TEXT_STYLE_HAPPY_TEXT } from "./styles";
import { RoundVideoPlayer } from "./round-video-player";

import { data as videoTranscripts } from '../assets/video/video-transcripts';

import bassIcon from '../assets/images/instrument-icons/insicon_bass.svg';
import brassIcon from '../assets/images/instrument-icons/insicon_brass.svg';
import celloIcon from '../assets/images/instrument-icons/insicon_cello.svg';
import clarinetIcon from '../assets/images/instrument-icons/insicon_clarinet.svg';
import fluteIcon from '../assets/images/instrument-icons/insicon_flute.svg';
import harpIcon from '../assets/images/instrument-icons/insicon_harp.svg';
import hornIcon from '../assets/images/instrument-icons/insicon_horn.svg';
import oboeIcon from '../assets/images/instrument-icons/insicon_oboe.svg';
import stringIcon from '../assets/images/instrument-icons/insicon_string.svg';
import timpaniIcon from '../assets/images/instrument-icons/insicon_timpani.svg';
import trumpetIcon from '../assets/images/instrument-icons/insicon_trumpet.svg';
import { Instrument } from "./types/instruments";
// import cymbalsIcon from '../assets/images/instrument-icons/insicon_cymbals.svg';
// import keyboardIcon from '../assets/images/instrument-icons/insicon_keyboard.svg';
//import choirIcon from '../assets/images/instrument-icons/insicon_choir.svg';
//import gongIcon from '../assets/images/instrument-icons/insicon_gong.svg';
//import percussionsectionIcon from '../assets/images/instrument-icons/insicon_percussionsection.svg';
import synthIcon from '../assets/images/instrument-icons/insicon_synth.svg';
//import woodwindIcon from '../assets/images/instrument-icons/insicon_woodwind.svg';

import bassOgg from '../assets/audio/bass.ogg';
import bassclarinetBassoonOgg from '../assets/audio/bassclarinet_bassoon.ogg';
import celloOgg from '../assets/audio/cello.ogg';
import clarinetOgg from '../assets/audio/clarinet.ogg';
import fluteOgg from '../assets/audio/flute.ogg';
import frenchHorn_1Ogg from '../assets/audio/french_horn_1.ogg';
import harpOgg from '../assets/audio/harp.ogg';
import oboeOgg from '../assets/audio/oboe.ogg';
import percussionCymGongTimpBassDrumOgg from '../assets/audio/percussion_cym_gong_timp_bass_drum.ogg';
import tromboneCimbassoOgg from '../assets/audio/trombone_cimbasso.ogg';
import trumpetOgg from '../assets/audio/trumpet.ogg';
import violaOgg from '../assets/audio/viola.ogg';
import violinOgg from '../assets/audio/violin.ogg';
import { RoundSoundPlayer } from "./round-sound-player";

function getInstrumentIcon(instrument: Instrument): undefined | [string, number] {
  switch (instrument) {
    case "Bass":
      return [bassIcon, -Math.PI / 8];
    case "Cello":
      return [celloIcon, -Math.PI / 8];
    case "Clarinet":
      return [clarinetIcon, Math.PI / 4];
    case "Flute":
      return [fluteIcon, -Math.PI / 8];
    case "French Horn":
      return [hornIcon, 0];
    case "Harp":
      return [harpIcon, 0];
    case "Bassoon":
    case "Oboe":
      return [oboeIcon, Math.PI / 4];
    case "Percussion":
      return [timpaniIcon, 0];
    case "Trombone":
    case "Trumpet":
      return [trumpetIcon, -Math.PI / 8];
    case "Tuba":
      return [brassIcon, 0];
    case "Viola":
      return [stringIcon, -Math.PI / 4];
    case "Violin":
      return [stringIcon, -Math.PI / 4];
    case "Synth":
      return [synthIcon, 0];
  }
}

function getInstrumentSound(instrument: Instrument): undefined | string {
  switch (instrument) {
    case "Bass":
      return bassOgg;
    case "Bassoon":
      return bassclarinetBassoonOgg;
    case "Cello":
      return celloOgg;
    case "Clarinet":
      return clarinetOgg;
    case "Flute":
      return fluteOgg;
    case "French Horn":
      return frenchHorn_1Ogg;
    case "Harp":
      return harpOgg;
    case "Oboe":
      return oboeOgg;
    case "Percussion":
      return percussionCymGongTimpBassDrumOgg;
    case "Trumpet":
      return trumpetOgg;
    case "Tuba":
    case "Trombone":
      return tromboneCimbassoOgg;
    case "Viola":
      return violaOgg;
    case "Violin":
      return violinOgg;
  }
}

const OUTER_RADIUS = 32;
const MEDIA_PLAYER_RADIUS = 64;
const INNER_RADIUS = 25;
const LINE_THICK = 2;
const BOX_LINE_THICK = 3.5;
const PLAY_BUTT_RADIUS = 16;
const PLAY_BUTT_SPACE = 8;
const BIO_CONTENT_PADDING = 15;
const BIO_CONTENT_SPACING = 15;
const BIO_BOX_X = 96;
const ANIM_SPEED = 0.09;
const WORD_AROUND_LINES = 20;

function makeHappyText(person: Biography) {
  if(!person.videos) return '';
  const usableVids = Object.entries(person.videos).filter(([k]) => !/outtake/i.test(k)).map(([,v]) => v);
  const usableVideos = Object.entries(videoTranscripts).filter(([k]) => usableVids.includes(k));
  return usableVideos.map(([,text]) => text).join(' ');
}

export class BioElement extends Container {
  private graphics: Graphics = new Graphics();
  private focusContent: Container = new Container();
  public isActive = false;
  public isFocused = false;
  public isVisited = false;
  private mediaPlayer: DisplayObject;
  private photo: Sprite;
  private animPhoto: AnimatedSprite;
  private photoContainer: Container;
  private isHover = false;
  private animFrame = 0;
  private happyText: string[] = [];
  private happyWordsContainer = new Container();
  private instrSpr: Sprite;
  private instSound: string;

  constructor(public person: Musician | Biography) {
    super();

    if(this.person.photos) {
      this.photoContainer = new Container();
      const photoMask = new Graphics();
      photoMask.beginFill(0xffffff).drawCircle(OUTER_RADIUS,OUTER_RADIUS,OUTER_RADIUS).endFill();
      this.photoContainer.addChild(photoMask);
      this.photoContainer.mask = photoMask;
      this.photo = Sprite.from(this.person.photos[0]);
      this.photo.width = OUTER_RADIUS*2;
      this.photo.height = OUTER_RADIUS*2;
      this.photoContainer.addChild(this.photo);
      this.addChild(this.photoContainer);
      photoMask.position.set(-OUTER_RADIUS,-OUTER_RADIUS);
      this.photo.position.set(-OUTER_RADIUS,-OUTER_RADIUS);
      this.photoContainer.zIndex = -2;

      if(this.person.photos.length > 1) {
        let toUse = this.person.photos.slice(1);
        this.animPhoto = AnimatedSprite.fromImages(
          [
            ...toUse,
            //toUse[toUse.length - 2]
          ]
        );
        this.animPhoto.scale.set(MEDIA_PLAYER_RADIUS / 256)
        this.animPhoto.animationSpeed = 0.1;
        this.animPhoto.loop = false;
        this.animPhoto.autoUpdate = false;
        this.animPhoto.stop();
        this.animPhoto.visible = true;
        this.animPhoto.position.set(-OUTER_RADIUS,-OUTER_RADIUS);
        this.photoContainer.addChild(this.animPhoto);
      }
    }


    if(isMusician(this.person)) {
      const icon = getInstrumentIcon(this.person.instrument);
      const sound = getInstrumentSound(this.person.instrument);
      if(icon && sound) {
        this.instrSpr = Sprite.from(icon[0]);
        this.instrSpr.rotation = icon[1];
        this.instSound = sound;
      }
    }

    this.sortableChildren = true;
    this.graphics.zIndex = -1;
    this.clearFocusContent();
    this.graphics.interactive = true;
    this.graphics.cursor = "pointer";
    this.draw();
    this.addChild(this.graphics);
    this.graphics.on("pointertap", this.focus.bind(this));
    this.graphics.on("pointerover", this.pointerOver.bind(this));
    this.graphics.on("pointerout", this.pointerOut.bind(this));

    if(this.person.videos) {
      this.happyText = makeHappyText(this.person).toLowerCase().split(/\s+/);
    }
  }

  pointerOver() {
    this.isHover = true;

    if(this.animPhoto) {
      //this.animPhoto.visible = false;
    }
  }

  pointerOut() {
    this.isHover = false;

    if(this.animPhoto && !this.isFocused) {
      this.animPhoto.visible = true;
    }
  }

  focus() {
    if(this.isFocused) return;
    this.isFocused = true;
    this.isVisited = true;
    this.zIndex = 1;

    this.animPhoto.visible = false;

    this.prepareFocusContet();
    this.happyWordsContainer.alpha = 0;
    new TWEEN.Tween({alpha: 0})
      .to({alpha: 1}, 1000)
      .easing( TWEEN.Easing.Quadratic.InOut )
      .onStart(() => {
        this.happyWordsContainer.alpha = 0;
      })
      .onUpdate(({alpha}) => {
        this.happyWordsContainer.alpha = alpha;
      })
      .start();

    new TWEEN.Tween({alpha: 0, scale: 1})
      .to({alpha: 1, scale: MEDIA_PLAYER_RADIUS/OUTER_RADIUS}, 400)
      .easing( TWEEN.Easing.Elastic.Out )
      .onStart(() => {
        this.photoContainer.zIndex = 1;
        this.focusContent.alpha = 0;
        this.focusContent.visible = true;
        this.photoContainer.scale.set(1);
      })
      .onUpdate(({alpha, scale}) => {
        this.focusContent.alpha = alpha;
        this.photoContainer.scale.set(scale);
      })
      .start();

    this.emit("focused");

    // Also activate if not already
    if(!this.isActive) {
      this.activate();
    } else {
      this.draw();
    }
  }

  unfocus() {
    if(!this.isFocused) return;
    this.isFocused = false;
    this.zIndex = 0;

    if(this.mediaPlayer) {
      this.removeChild(this.mediaPlayer);
      this.mediaPlayer.destroy();
      delete this.mediaPlayer;
    }

    this.animPhoto.visible = true;

    new TWEEN.Tween({alpha: 1})
      .to({alpha: 0}, 100)
      .easing( TWEEN.Easing.Quadratic.InOut )
      .onStart(() => {
        this.happyWordsContainer.alpha = 1
      })
      .onUpdate(({alpha}) => {
        this.happyWordsContainer.alpha = alpha;
      })
      .start();

    new TWEEN.Tween({alpha: 1, scale: this.photoContainer.scale.x})
      .to({alpha: 0, scale: 1}, 100)
      .easing( TWEEN.Easing.Cubic.Out )
      .onStart(() => {
        this.focusContent.alpha = 1;
        this.focusContent.visible = true;
      })
      .onUpdate(({alpha, scale}) => {
        this.photoContainer.scale.set(scale);
        this.focusContent.alpha = alpha;
      })
      .onComplete(() => {
        this.photoContainer.zIndex = -2;
        this.focusContent.visible = false;
        this.clearFocusContent();
        this.draw();
      })
      .start();

    this.emit("unfocused");
  }

  activate() {
    if(this.isActive) return;
    this.isActive = true;
    this.emit("activated");
    this.draw();
  }

  deactivate() {
    if(!this.isActive) return;
    this.isActive = false;
  }

  clearFocusContent() {
    if(this.focusContent) {
      this.removeChild(this.focusContent);
      this.focusContent.destroy();
      this.focusContent = new Container();
      this.addChild(this.focusContent);
      this.happyWordsContainer.removeChildren();
    }
    this.focusContent.visible = false;
  }

  prepareFocusContet() {
    if(this.focusContent.children.length > 0) return;

    this.focusContent.interactive = true; // prevents click un focus

    let drawOnLeft = this.position.x >= 400;

    const color = 0xffffff;//(isMusician(this.person) && INSTRUMENT_COLORS[this.person.instrument]) || DEFAULT_INSTRUMENT_COLOR;
    const calculatedOuterRad = this.person.bioText || this.person.videos ? OUTER_RADIUS : INNER_RADIUS;

    const graphicsBkg = new Graphics();
    this.focusContent.addChild(graphicsBkg);

    // Musicians Name with instrument
    const bioName = new Text(`${this.person.name}`, TEXT_STYLE_H2);
    bioName.anchor.set(0, 0);
    if(drawOnLeft) bioName.anchor.set(1, 0);
    this.focusContent.addChild(bioName);

    this.happyWordsContainer.removeChildren();
    //this.addChild(this.happyWordsContainer);
    this.happyWordsContainer.zIndex = -3;

    // let startWord = 0;
    // for (let i = 0; i < WORD_AROUND_LINES; i+= 1) {
    //   const tStart = Math.floor((i/WORD_AROUND_LINES) * this.happyText.length);
    //   const tEnd = tStart + Math.min(this.happyText.length, Math.floor(Math.random()*10 + 2))
    //   const words = this.happyText.slice(tStart, tEnd).join(" ");
    //   const t = new Text(words, TEXT_STYLE_HAPPY_TEXT);
    //   t.anchor.set(0.5, 0.5);
    //   const theta = (i/WORD_AROUND_LINES) * 2 * Math.PI;
    //   this.happyWordsContainer.addChild(t);
    //   t.rotation = 0;//theta + Math.PI;
    //   t.position.set(-200 + Math.random() *400, i * 20 - WORD_AROUND_LINES*20/2);//set(Math.cos(theta) * (VIDEO_PLAYER_RADIUS + 10), Math.sin(theta) * (VIDEO_PLAYER_RADIUS + 10))
    // }

    // let bioText = this.happyText.join(" ");

    let BIO_WIDTH = bioName.width + BIO_CONTENT_PADDING * 2;

    // Subtitle
    if(this.person.title) {
      const subTitle = new Text(`${this.person.title}`, TET_STYLE_BIO_SUBTITLE);
      subTitle.anchor.set(0, 0);
      this.focusContent.addChild(subTitle);
      BIO_WIDTH = Math.max(BIO_WIDTH, subTitle.width + BIO_CONTENT_PADDING * 2);
      if(drawOnLeft) {
        subTitle.anchor.set(1, 0);
        subTitle.position.set(-MEDIA_PLAYER_RADIUS - 20, BIO_CONTENT_PADDING + 16 + BIO_CONTENT_SPACING);
      } else {
        subTitle.position.set(MEDIA_PLAYER_RADIUS + 20, BIO_CONTENT_PADDING + 16 + BIO_CONTENT_SPACING);
      }
    }

    if(drawOnLeft) {
      bioName.position.set(-MEDIA_PLAYER_RADIUS - 10, BIO_CONTENT_PADDING);
    } else {
      bioName.position.set(MEDIA_PLAYER_RADIUS + 10, BIO_CONTENT_PADDING);
    }

    let countPlayButtons = 0;
    if(this.person.videos && Object.keys(this.person.videos).length) {
      Object.entries(this.person.videos)
        .filter(([k]) => !/outtake/i.test(k))
        .map(([key,url], i) => {
          let butt = new Graphics();
          const color = videoColor(key);
          butt.beginFill(color,1).drawCircle(0,0,PLAY_BUTT_RADIUS).endFill();


          butt.beginFill(0xffffff);
          const r = PLAY_BUTT_RADIUS * 0.5;
          const c = Math.cos(Math.PI * 2/3);
          const s = Math.sin(Math.PI * 2/3);
          butt.drawPolygon([
            new Point(c * r, -s * r),
            new Point(r, 0),
            new Point(c * r, s * r),
          ])
          butt.endFill();

          butt.interactive = true;
          butt.cursor = "pointer";
          butt.on("pointerdown", () => {
            this.cleanUpPlayer();

            const mediaPlayer = new RoundVideoPlayer(url, MEDIA_PLAYER_RADIUS, color);
            mediaPlayer.once("ended", this.cleanUpPlayer.bind(this))
            this.addChild(mediaPlayer);
            mediaPlayer.zIndex = 99;

            mediaPlayer.position.set(-MEDIA_PLAYER_RADIUS,-MEDIA_PLAYER_RADIUS);
            mediaPlayer.autoplay = true;
            mediaPlayer.preload();

            this.mediaPlayer = mediaPlayer;

          });

          this.focusContent.addChild(butt);
          countPlayButtons++;
          if(drawOnLeft) {
            butt.position.set(-(MEDIA_PLAYER_RADIUS + BIO_CONTENT_PADDING * 2 + (PLAY_BUTT_RADIUS * 2 + PLAY_BUTT_SPACE) * i), MEDIA_PLAYER_RADIUS * 2 - PLAY_BUTT_RADIUS - BIO_CONTENT_PADDING);
          } else {
            butt.position.set(MEDIA_PLAYER_RADIUS + BIO_CONTENT_PADDING * 2 + (PLAY_BUTT_RADIUS * 2 + PLAY_BUTT_SPACE) * i, MEDIA_PLAYER_RADIUS * 2 - PLAY_BUTT_RADIUS - BIO_CONTENT_PADDING);
          }
        });
    }

    if(this.instrSpr && this.instSound) {
      let buttRadius = 16;
      const instrumentButt = new Container();
      instrumentButt.interactive = true;
      instrumentButt.cursor = "pointer";
      this.instrSpr.anchor.set(0.5,0.5);
      this.instrSpr.alpha = 0.7;
      this.instrSpr.blendMode = BLEND_MODES.ADD;
      this.instrSpr.scale.set(1.2*buttRadius/this.instrSpr.texture.width);

      let butt = new Graphics();
      const color = 0x555555;
      butt.beginFill(color,1).drawCircle(0,0,buttRadius).endFill();

      countPlayButtons++;
      instrumentButt.addChild(butt);
      instrumentButt.addChild(this.instrSpr);

      instrumentButt.on("pointerdown", () => {
        this.cleanUpPlayer();
        const playerImage = Sprite.from(this.instrSpr.texture);
        playerImage.rotation = this.instrSpr.rotation;
        const mediaPlayer = new RoundSoundPlayer(this.instSound, playerImage, MEDIA_PLAYER_RADIUS, 0xcccccc);
        mediaPlayer.once("ended", this.cleanUpPlayer.bind(this))
        this.addChild(mediaPlayer);
        mediaPlayer.zIndex = 99;

        mediaPlayer.position.set(-MEDIA_PLAYER_RADIUS,-MEDIA_PLAYER_RADIUS);
        mediaPlayer.autoplay = true;
        mediaPlayer.preload();

        this.mediaPlayer = mediaPlayer;

      });

      this.focusContent.addChild(instrumentButt);
      if(drawOnLeft) {
        instrumentButt.position.set(-(MEDIA_PLAYER_RADIUS + BIO_CONTENT_PADDING * 2 + (PLAY_BUTT_RADIUS * 2 + PLAY_BUTT_SPACE) * (countPlayButtons - 1)), MEDIA_PLAYER_RADIUS * 2 - PLAY_BUTT_RADIUS - BIO_CONTENT_PADDING);
      } else {
        instrumentButt.position.set(MEDIA_PLAYER_RADIUS + BIO_CONTENT_PADDING * 2 + (PLAY_BUTT_RADIUS * 2 + PLAY_BUTT_SPACE) * (countPlayButtons - 1), MEDIA_PLAYER_RADIUS * 2 - PLAY_BUTT_RADIUS - BIO_CONTENT_PADDING);
      }
    }

    BIO_WIDTH = Math.max(BIO_WIDTH, countPlayButtons * (PLAY_BUTT_RADIUS * 2 + PLAY_BUTT_SPACE));

    // Rectangle background
    graphicsBkg.beginFill(0, 0.95);
    graphicsBkg.lineStyle(BOX_LINE_THICK, 0xb3b3b3, 1, 0);
    // Main bkg for text
    if(drawOnLeft) {
      graphicsBkg.drawRoundedRect(-BIO_WIDTH - BIO_BOX_X, 0, BIO_WIDTH + BIO_BOX_X, MEDIA_PLAYER_RADIUS*2, 2);
    } else {
      graphicsBkg.drawRoundedRect(0, 0, BIO_WIDTH + BIO_BOX_X, MEDIA_PLAYER_RADIUS*2, 2);
    }
    graphicsBkg.endFill();
    // Draw connecting line

    this.focusContent.position.set(0, -this.focusContent.height / 2);

  }

  cleanUpPlayer() {
    if(this.mediaPlayer) {
      this.mediaPlayer.off("ended", this.cleanUpPlayer.bind(this));
      this.removeChild(this.mediaPlayer);
      this.mediaPlayer.destroy();
      delete this.mediaPlayer;
    }
  }

  onTick(deltams) {
    if(this.animPhoto && this.animPhoto.visible && !this.isHover) {
      let lastFrame = Math.floor(this.animFrame);
      this.animFrame += ANIM_SPEED * deltams;
      if(Math.floor(this.animFrame) !== lastFrame) {
        let randomNextFrame = Math.floor(Math.random()*(this.animPhoto.totalFrames - 1)) + 1
        this.animPhoto.gotoAndStop((this.animPhoto.currentFrame + randomNextFrame) % this.animPhoto.totalFrames);
      }
    }

    if(this.happyWordsContainer) {
      this.happyWordsContainer.children.forEach((t, i, arr) => {
        if(t instanceof Text) {
          let speed = 0.5 - ((i % 5) / 5);
          t.position.x += deltams*(0.2*speed);
          if(t.position.x < -100) {
            t.alpha = Math.max(0, (t.position.x + 200) / 100);
            if(t.position.x < -200) {
              t.position.x += 400;
            }
          }
          if(t.position.x > 100) {
            t.alpha = Math.max(0, (200 - t.position.x) / 100);
            if(t.position.x > 200) {
              t.position.x -= 400;
            }
          }
        }
      });
    }
  }

  draw() {
    this.graphics.clear();

    const color = 0xffffff;//(isMusician(this.person) && INSTRUMENT_COLORS[this.person.instrument]) || DEFAULT_INSTRUMENT_COLOR;
    const alpha = this.isVisited && !this.isFocused ? 0.1 : 1;

    this.graphics.beginFill(0, 0.3);

    if(!this.isFocused) {
      this.graphics.drawCircle(0, 0, OUTER_RADIUS);
    }

    this.graphics.endFill();


    this.graphics.lineStyle(LINE_THICK, color, alpha/2, 0);

    if(!this.isFocused) {
      if(this.person.bioText || this.person.videos) {
        this.graphics.drawCircle(0, 0, OUTER_RADIUS);
      }
    }
  }
}